/* src/components/Header.css */

/*    */
.header {
  box-sizing: border-box;
  position: fixed; /*    */
  top: 0;
  left: 280px; /*     */
  width: calc(100% - 280px); /*      */
  height: 140px; /*   */
  z-index: 10; /*     */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto auto;
  gap: 10px;
  padding: 30px 20px;
  background-color: transparent;
  border-bottom: 1px solid #a7a5a53d;
}

/*   : ,  ,  */
/* .header-left,
.header-center,
.header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
} */

/*    */
.header-left h1 {
  position: relative;
  z-index: 1;
  font-family: 'Roboto Slab', serif;
  font-size: 24px;
  margin: 0;
  color: #46485298;
  overflow: hidden;
}

.header-left {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-left: 10%;
}

.header-right {
  margin-left: 33%;
  align-items: center;
}

/*   */
.header-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*   */
.date-navigation {
  display: flex;
  align-items: center;
}

/*    */
.selected-date {
  font-size: 1.5rem;
  padding: 0 10px;
  font-weight: 700;
  color: #050505;
}

/*   */
.weekday {
  font-size: 1.2rem;
  padding-left: 5px;
  color: #555555;
}

/*    */
.weekend {
  color: #ff00008e;
}

/*    */
.arrow-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  color: #3f3f3f78;
  transition: color 0.3s ease, transform 0.3s ease;
}

.arrow-button:hover {
  color: #194fcdaa;
  transform: scale(1.1);
}

/*   */
.header-right {
  justify-content: center;
  align-items: center;
}

/*      */
.quick-create-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

/*      */
.quick-button {
  padding: 6px 12px;
  font-size: 1.1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #6287ace1;
  color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.quick-button:hover {
  background-color: #007cb576;
}

/* ""     */
.quick-button-green {
  background-color: #4caf4fe9;
  color: #ffffff;
}

.quick-button-green:hover {
  background-color: #45a04979;
}
/* OTA   */
.header-ota-status {
  grid-column: 1 / 4; /*       */
  grid-row: 3 / 4; /*     */
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

/*   : OTA  */
.header-ota-toggles {
  grid-column: span 3; /*       3    */
  display: flex;
  justify-content: flex-start; /*   */
  width: 100%; /*    */
  margin-top: 10px;
  overflow: hidden; /*    */
  white-space: nowrap; /*     */
  text-overflow: ellipsis; /*   '...'  */
}

/* OTA   */
.ota-toggles {
  display: flex;
  gap: 15px;
}

.ota-toggles label {
  font-size: 14px;
  display: flex;
  align-items: center;
}

.ota-toggles input {
  margin-right: 5px;
}

/* OTA   */
.header-ota-status {
  grid-column: span 3;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.ota-status-item {
  margin-right: 20px;
  font-size: 16px;
  display: flex;
  align-items: center;
}

.status-lamp {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left: 8px;
}

.status-lamp.green {
  background-color: rgb(131, 239, 131);
}

.status-lamp.gray {
  background-color: gray;
}

/*   () */
@media (max-width: 1600px) {
  .header {
    grid-template-columns: repeat(1, 1fr); /*    3  */
  }
  .header-left {
    display: none;
  }

  .header-center {
    flex: 1.5;
  }

  .header-right {
    display: none;
    overflow: hidden;
  }

  .selected-date {
    font-size: 1.3rem;
    overflow: hidden;
  }

  .weekday {
    font-size: 1.1rem;
    overflow: hidden;
  }

  .quick-button {
    padding: 6px 12px;
    font-size: 1rem;
    overflow: hidden;
  }

  .ota-status-item {
    /* display: none; */
    overflow: hidden;
    font-size: 14px;
  }
}
