/* src/components/RoomGrid.css */

/* CSS   */
:root {
  --color-primary-deep-blue: #677ed9;
  --color-primary-soft-green: #27ae60;
  --color-primary-elegant-gray: #95a5a6;
  --color-accent-gold: #f1c40f;
  --color-accent-coral: #939596;
  --color-background-light: #f3f6f8;
  --color-text-dark: #0a0a0a;
  --color-text-white: #ffffff;
  --color-border-default: #3c3c3c85;
  --color-border-hover-green: #1e8449;
  --color-border-hover-blue: #1a5276;
  --color-border-hover-coral: #c0392b;
  --color-bright-yellow: #ffd700;

  --room-card-background-color: var(--color-background-light);
  --room-card-text-color: var(--color-text-dark);
}

/*   flex,  edit-panel-left(30%),  grid-wrapper() */
body,
html {
  margin: 0;
  padding: 0;
}

.edit-panel-left {
  width: 30%;
  background-color: transparent;
  border-right: 1px solid rgb(186, 187, 188);
  box-sizing: border-box;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.edit-panel-left h2 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: bold;
}

.edit-panel-left label {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  margin-bottom: 5px;
  color: var(--color-text-dark);
}

.edit-panel-left input,
.edit-panel-left select {
  margin-top: 5px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.9rem;
  background-color: lightgrey;
}

.edit-panel-left .panel-actions {
  display: flex;
  gap: 5px;
  margin-top: 10px;
}

.edit-panel-left .panel-actions button {
  font-size: 0.9rem;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

/* Grid wrapper */
.grid-wrapper {
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
  background-color: transparent;
  position: relative;
}

/* Grid container */
.grid-container {
  background-color: transparent;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 10px;
  margin-top: 20px;
}

.grid-container.halo-effect {
  animation: haloAnimation 10s ease-out;
}

@keyframes haloAnimation {
  0% {
    box-shadow: 0 0 10px rgba(234, 199, 86, 0);
  }
  100% {
    box-shadow: 0 0 0 rgba(255, 193, 7, 0);
  }
}

/* evening-mode   */
.grid-container.evening-mode {
  --room-card-background-color: #000000;
  --room-card-text-color: var(--color-text-white);
}

/* Room card */
.room-card {
  padding: 15px;
  border: 3px solid var(--color-border-default);
  border-radius: 12px;
  margin: 1px;
  text-align: left;
  display: flex;
  flex-direction: column;
  position: relative;
  background: var(--room-card-background-color);
  color: var(--room-card-text-color);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: transform 5.4s ease, box-shadow 0.4s ease, opacity 0.4s ease;
  min-height: 330px;
}

.room-card h3 {
  font-size: 1.3rem;
  margin-top: 2px;
  margin-bottom: 8px;
  font-family: 'Poppins', sans-serif;
  color: var(--room-card-text-color);
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.room-card p {
  margin: 2px 0;
  font-size: 0.95rem;
  color: var(--room-card-text-color);
  font-weight: 500;
}

/* ''   */
.site-info.onsite-reservation {
  color: black;
}

.grid-container.evening-mode .site-info.onsite-reservation {
  color: var(--color-bright-yellow);
}

.grid-container.evening-mode .room-card,
.grid-container.evening-mode .room-card h3,
.grid-container.evening-mode .room-card p,
.grid-container.evening-mode .room-card p.site-info {
  color: var(--color-text-white) !important;
  font-weight: bold;
}

.room-card.border-primary-soft-green {
  border: 3px solid var(--color-primary-soft-green);
}
.room-card.border-primary-deep-blue {
  border: 3px solid var(--color-primary-deep-blue);
}
.room-card.border-accent-coral {
  border: 3px solid var(--color-accent-coral);
}

.flip-container.auto-flip .room-card-inner {
  animation: autoFlip 2s ease-in-out;
}

@keyframes autoFlip {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(-30deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

/* Hover  */
.room-card:hover {
  transform: rotateX(1deg) translateX(-1px);
  z-index: 10;
}

/* Hover    */
.room-card.border-primary-soft-green:hover {
  border: 1px solid var(--color-border-hover-green);
}
.room-card.border-primary-deep-blue:hover {
  border: 1px solid var(--color-border-hover-blue);
}
.room-card.border-accent-coral:hover {
  border: 1px solid var(--color-border-hover-coral);
}

/* Hover    */
.room-card::before {
  content: '';
  position: absolute;
  top: 1px;
  left: 1px;
  width: calc(100% + 1px);
  height: calc(100% + 1px);
  background: radial-gradient(
    circle,
    transparent,
    rgba(131, 153, 184, 0.596) 100%
  );
  transition: opacity 0.4s ease, box-shadow 0.4s ease;
  opacity: 0;
  pointer-events: none;
  border-radius: 5%;
  cursor: pointer;
  z-index: -1;
}

.room-card:hover::before {
  opacity: 1;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.8);
}

/* Fade-out/Fade-in      */

/*   */
.button-group {
  display: inline-flex;
  gap: 5px;
  margin-left: 10px;
  align-items: center;
}

/*    */
.action-button {
  font-size: 0.8rem;
  padding: 3px 6px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

/*   */
.delete-button {
  background-color: var(--color-accent-coral);
  color: var(--color-text-white);
}
.delete-button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
}

/*   */
.confirm-button {
  background-color: var(--color-primary-deep-blue);
  color: var(--color-text-white);
}
.confirm-button:hover {
  background-color: var(--color-border-hover-blue);
  transform: scale(1.05);
}

/*   */
.edit-button {
  background-color: var(--color-primary-soft-green);
  color: var(--color-text-white);
}
.edit-button:hover {
  background-color: var(--color-border-hover-green);
  transform: scale(1.05);
}

/*   */
.small-button {
  opacity: 0.5;
  font-size: 0.75rem;
  padding: 2px 4px;
}
.small-button:hover {
  opacity: 1;
}

/*   */
.flip-container {
  perspective: 1000px;
  height: 100%;
}
.room-card-inner {
  transition: transform 0.6s;
  transform-style: preserve-3d;
  height: 100%;
}
.flipped .room-card-inner {
  transform: rotateY(180deg);
}

.room-card-front,
.room-card-back {
  position: absolute;
  width: 100%;
  backface-visibility: hidden;
  height: 100%;
  border-radius: 12px;
}

.room-card-back {
  transform: rotateY(180deg);
  background-color: var(--room-card-background-color);
  color: var(--room-card-text-color);
  padding: 15px;
  box-sizing: border-box;
}

/*   */
.confirmed-label {
  background-color: var(--color-primary-soft-green);
  color: var(--color-text-white);
  padding: 2px 5px;
  border-radius: 3px;
  font-size: 0.75rem;
  margin-left: 5px;
  font-weight: bold;
}

/* Highlighted overlay */
.highlighted-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  border: 2px solid var(--color-accent-gold);
  box-shadow: 0 0 10px var(--color-accent-gold);
  pointer-events: none;
  z-index: 5;
  animation: overlayPulse 2s infinite;
}

@keyframes overlayPulse {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

/* site-info-wrapper */
.site-info-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  padding-top: 10px;
  border-top: 1px solid #666;
  box-sizing: border-box;
  width: 100%;
}

.site-info {
  font-weight: bold;
  color: var(--room-card-text-color);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.invoice-icon-button-back {
  background: transparent;
  border: none;
  cursor: pointer;
  color: #666e76;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease, transform 0.2s ease;
}
.invoice-icon-button-back:hover {
  color: var(--color-accent-gold);
  transform: scale(1.2);
}

.room-card-front {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.card-content {
  flex: 1;
}

.payment-method {
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 5px;
  font-weight: 500;
}

.payment-icon {
  vertical-align: middle;
}

.room-card {
  transition: background-color 0.3s ease, box-shadow 0.3s ease,
    border-color 0.3s ease;
}

.room-card.highlighted {
  background-color: rgba(70, 103, 148, 0.596);
  border-color: var(--color-accent-gold);
  box-shadow: 0 0 10px var(--color-accent-gold);
  animation: highlightPulse 2s infinite;
}

@keyframes highlightPulse {
  0% {
    background-color: rgba(70, 103, 148, 0.596);
  }
  50% {
    background-color: rgba(255, 215, 0, 0.5);
  }
  100% {
    background-color: rgba(70, 103, 148, 0.596);
  }
}

/*       */
.grid-container.editing-active .room-card:not(.edit-mode) {
  opacity: 0.2;
  pointer-events: none;
  filter: blur(2px);
}

/*    30  */
.room-card.edit-mode .room-card-inner {
  transform: rotateY(-30deg) !important;
  animation: highlightPulse 12s;
}

/*     */
.onsite-reservation {
  color: rgba(136, 169, 186, 0.956);
  font-weight: 800;
}
.edit-panel-left {
  width: 25%;
  max-width: 400px;
  min-width: 300px;
  height: 840px;
  background-color: transparent;
  border: transparent;
  box-sizing: border-box;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 10px;
  border-radius: 20px;
  transform: translateX(-120%); /*    */
  transition: transform 0.3s ease-in-out; /*   */
  background: rgb(199, 199, 198);
  z-index: 999;
}

.edit-panel-left.open {
  transform: translateX(0); /* open      */
}

/*    Flexbox     */
.edit-panel-left .panel-actions {
  display: flex;
  justify-content: space-around; /*   */
  align-items: center;
  margin-top: 10px; /*    */
}

/*       */
.edit-panel-left .panel-actions button {
  font-size: 1em; /*    */
  padding: 10px 10px; /*   */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin-left: 10px; /*    */
  flex: 1; /*     */
  max-width: 150px; /*    (  ) */
}

/*    */
.edit-panel-left .panel-actions .save-button {
  background-color: var(--color-primary-soft-green);
  color: var(--color-text-white);
}

.edit-panel-left .panel-actions .save-button:hover {
  background-color: #239b56;
  transform: scale(1.05); /*    */
}

/*    */
.edit-panel-left .panel-actions .cancel-button {
  background-color: var(--color-accent-coral);
  color: var(--color-text-white);
}

.edit-panel-left .panel-actions .cancel-button:hover {
  background-color: #a05252;
  transform: scale(1.05);
}

/*     */
.edit-panel-left .panel-actions button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
}

/*     */

/*   */
.memo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #919191;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  margin-bottom: 5px;
  box-sizing: border-box;
}

.memo-edit-button,
.memo-edit-actions button {
  background: transparent;
  color: #b4b3b3;
  border: none;
  padding: 5px 10px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 5px;
}

.memo-edit-button:hover,
.memo-edit-actions button:hover {
  background: transparent;
  color: rgb(248, 3, 3);
  scale: 1.2;
}

/*    */
.memo-body {
  background-color: transparent;
  color: #000;
  padding: 5px;
  box-sizing: border-box;
  height: 85%; /*   (  ) */
  overflow: hidden;
  margin-top: 10px;
  border-radius: 5px;
}

.memo-text-display {
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 0.9rem;
  line-height: 1.3;
}

/*  textarea */
.memo-textarea {
  width: 100%;
  height: 100%;
  background: transparent;
  color: #000;
  font-size: 0.9rem;
  border: none;
  border-radius: 4px;
  resize: none;
  outline: none;
  box-sizing: border-box;
  padding: 5px;
}

.room-card.onsite-created {
  animation: highlightPulse 10s;
  background-color: transparent;
}

@keyframes highlightPulse {
  0% {
    box-shadow: 0 0 50px #8bf387;
  }
  100% {
    box-shadow: 0 0 0 #c4f4c3;
  }
}
